.article {
  color: $blue;
  padding: 20px 0;
  margin: 20px 0;
  border-top: 1px solid $darkgray;
  border-bottom: 1px solid $darkgray;

  &__title {
    font-size: 18px;
    font-weight: 800;
    text-align: center;
  }

  & p {
    margin-top: 30px;
    font-weight: 600;
    line-height: 1.5em;
  }
}
